.spinner1 {
  width: 100px;
  height: 100px;
  background-color: #51C62B;
  opacity: 0.7;
  margin: 100px auto;
  animation: sk-rotateplane 1.2s 4 ease-in-out;
}

@keyframes sk-rotateplane {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

.spinner2 {
  width: 100px;
  height: 100px;
  position: relative;
  margin: 100px auto;
}

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #DFC600;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  animation: sk-bounce 2.0s 4 ease-in-out;
}

.double-bounce2 {
  animation-delay: -1.0s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

.sk-folding-cube {
  margin: 20px auto;
  width: 100px;
  height: 100px;
  position: relative;
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  transform: scale(1.1);
}

.sk-folding-cube .sk-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #E08800;
  opacity: .6;
  animation: sk-foldCubeAngle 2.4s 4 linear both;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}

.sk-folding-cube .sk-cube2 {
  transform: scale(1.1) rotateZ(90deg);
}

.sk-folding-cube .sk-cube3 {
  transform: scale(1.1) rotateZ(180deg);
}

.sk-folding-cube .sk-cube4 {
  transform: scale(1.1) rotateZ(270deg);
}

.sk-folding-cube .sk-cube2:before {
  animation-delay: 0.3s;
}

.sk-folding-cube .sk-cube3:before {
  animation-delay: 0.6s;
}

.sk-folding-cube .sk-cube4:before {
  animation-delay: 0.9s;
}

@keyframes sk-foldCubeAngle {
  0%, 10% {
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 99% {
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}

.spinner4 {
  margin: 100px auto;
  width: 100px;
  height: 100px;
  text-align: center;
  font-size: 20px;
}

.spinner4 > div {
  background-color: #fff;
  height: 100%;
  width: 7px;
  display: inline-block;
  animation: sk-stretchdelay 1.2s 4 ease-in-out;
}

.spinner4 .rect2 {
  animation-delay: -1.1s;
}

.spinner4 .rect3 {
  animation-delay: -1.0s;
}

.spinner4 .rect4 {
  animation-delay: -0.9s;
}

.spinner4 .rect5 {
  animation-delay: -0.8s;
}

@keyframes sk-stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
  }
  20% {
    transform: scaleY(1);
  }
}

.demo-input {
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid #fff;
  color: #f5f5f5;
  font-size: 1rem;
  padding: 1rem;
  transition: background-color 0.4s, border 0.2s, box-shadow 0.4s, transform 0.4s cubic-bezier(0.85, -0.4, 0.29, 1.44);
}

.demo-input:focus {
  background: rgba(0, 0, 0, 0.8);
  border: 1px solid limegreen;
  transform: scale(1.5);
  box-shadow: 2px 1px 10px 10px rgba(255, 255, 255, 0.3);
}

.app_layout .box_color:nth-child(5) {
  background-color: rgba(255, 255, 255, 0.2);
}

.app_layout .box_color:nth-child(6) {
  background-color: rgba(255, 255, 255, 0.4);
}

.app_layout .box_color:nth-child(7) {
  background-color: rgba(255, 255, 255, 0.6);
}

.app_layout .box_color:nth-child(8) {
  background-color: rgba(255, 255, 255, 0.8);
}

body {
  margin: 0;
  font-family: "Open Sans", Arial;
  min-height: 100vh;
  background-color: #212121;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}

body.homepage {
  background-image: url(/images/trees_road.jpg);
}

body.grid_one_page {
  background-image: url(/images/lonely_tree.jpg);
}

.box {
  padding: 1em;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 200%;
  text-transform: uppercase;
}

.box1 {
  color: #51C62B;
}

.box2 {
  color: #DFC600;
}

.box3 {
  color: #E08800;
}

.box4 {
  color: #802503;
}

.box_color {
  animation-timing-function: cubic-bezier(0, 1, 1, 1);
}

/** index.html **/
.app_layout {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 50vh 50vh;
}

/** grid1.html **/
.grid {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: minmax(200px, auto) 400px minmax(300px, auto);
  grid-auto-rows: minmax(300px, auto);
  grid-gap: 20px;
  min-height: 100vh;
}

.grid_box {
  background-color: rgba(255, 255, 255, 0.3);
  border: 1px solid #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  color: #f5f5f5;
  font-size: 250%;
}

@media screen and (max-width: 1024px) {
  .app_layout {
    grid-template-columns: 1fr;
    grid-template-rows: 25vh 25vh 25vh 25vh;
  }
}
